<template>
    <div class="d-flex flex-direction:row flex-wrap ma-2">
        <div class="controls_container">
            <v-btn class="mb-2" raised color="#1565c0" dark>Download Image</v-btn>
            <v-slider min="0" max="255" value="255" v-model="opacity">
                <template v-slot:label>
                    <v-btn fab x-small>O</v-btn>
                </template>
            </v-slider>

            <v-slider min="0" max="255" value="0" v-model="red">
                <template v-slot:label>
                    <v-btn fab x-small color="#d50000" dark>R</v-btn>
                </template>
            </v-slider>

            <v-slider min="0" max="255" value="0" v-model="green">
                <template v-slot:label>
                    <v-btn fab x-small color="#1b5e20" dark>G</v-btn>
                </template>
            </v-slider>

            <v-slider min="0" max="255" value="0" v-model="blue">
                <template v-slot:label>
                    <v-btn fab x-small color="#1a237e" dark>B</v-btn>
                </template>
            </v-slider>
            <v-btn class="mb-2" raised color="#1565c0" dark @click.stop="selectImage">Select Image</v-btn>
            <input type="file" id="file" accept="image/*" @change.stop="loadImage" v-show="false">

        </div>

        <div id="canvas_container">
            <div class="ml-2"><canvas width="100%" height="100%" id="canvas"></canvas></div>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                opacity:255,
                red:0,
                green:0,
                blue:0,
                //OLD VALUES
                oldOpacity:255,
                oldRed:0,
                oldGreen:0,
                oldBlue:0,
            }
        },
        created(){
            window.addEventListener('resize',()=>{
                let cont = document.getElementById('canvas');
                cont.height=window.innerHeight*0.95;
                cont.width=window.innerWidth*0.83;
                if(document.getElementById('file').files[0])
                {
                    this.loadImage();
                }
            });
        },
        mounted(){
            let cont = document.getElementById('canvas');
                cont.height=window.innerHeight*0.95;
                cont.width=window.innerWidth*0.83;
        },
        methods:{
            selectImage(){
                this.red=0;
                this.blue=0;
                this.green=0;
                document.getElementById('file').click();
            },
            //METHOD TO LOAD SELECTED IMAGES
            loadImage(){
                let imagefile = document.getElementById('file').files[0];
                let reader = new FileReader();
                reader.onload=(event)=>{
                    let image = new Image();
                    image.src = event.target.result;
                    image.onload=()=>{
                        let canvas = document.getElementById('canvas');
                        var ctx = canvas.getContext("2d");
                        ctx.clearRect(0,0,canvas.width,canvas.height)
                        ctx.drawImage(image , 0,0,canvas.width,canvas.height)
                    }
                }
                reader.readAsDataURL(imagefile);
            },
            updateImage(){
                let canvas=document.getElementById('canvas');
                var context = canvas.getContext("2d");
                let imageData = context.getImageData(0,0,canvas.width,canvas.height);
                for(var i=0; i<imageData.data.byteLength; i+=4)
                   {
                   if(this.red){
                    if(this.red>this.oldRed && (imageData.data[i+0]+(this.red-this.oldRed))<256)
                    {
                        imageData.data[i+0]+=(this.red-this.oldRed)
                    }
                    else if(this.red<this.oldRed && imageData.data[i+0]>0)
                    {
                        imageData.data[i+0]-=(this.oldRed-this.red)
                    }
                }
                   if(this.green){
                    if(this.green>this.oldGreen && (imageData.data[i+1]+(this.green-this.oldGreen))<256)
                    {
                        imageData.data[i+1]+=(this.green-this.oldGreen)
                    }
                    else if(this.green<this.oldGreen && imageData.data[i+1]>0)
                    {
                        imageData.data[i+1]-=(this.oldGreen-this.green)
                    }
                }
                   if(this.blue){
                    if(this.blue>this.oldBlue && (imageData.data[i+2]+(this.blue-this.oldBlue))<256)
                    {
                        imageData.data[i+2]+=(this.blue-this.oldBlue)
                    }
                    else if(this.blue<this.oldBlue && imageData.data[i+2]>0)
                    {
                        imageData.data[i+2]-=(this.oldBlue-this.blue)
                    }
                }
                  if(this.opacity){
                    
                    if(this.opacity>this.oldOpacity && (imageData.data[i+3]+(this.opacity-this.oldOpacity))<256)
                    {
                        imageData.data[i+3]+=(this.opacity-this.oldOpacity)
                    }
                    else if(this.opacity<this.oldOpacity && imageData.data[i+3]>0)
                    {
                        imageData.data[i+3]-=(this.oldOpacity-this.opacity)
                    }
                   // this.calculateState(this.opacity,this.oldOpacity,imageData.data[i+3])
                }
            }
                context.putImageData(imageData,0,0)
        }
        
        },
        watch:{
            opacity(newOpacity,oldOpacity){
                this.oldOpacity=oldOpacity;
               this.updateImage()
            },

            red(newRed,oldRed){
                this.oldRed=oldRed;
                this.updateImage()
            },
            green(newGreen,oldGreen){
                this.oldGreen=oldGreen;
                this.updateImage()
            },
            blue(newBlue,oldBlue){
                this.oldBlue=oldBlue;
                this.updateImage()
            }
        }
    }
</script>
<style>
#canvas{
    border:1px solid #1565c0;
}
@media screen and (min-width: 600px){
    #canvas_container{
        width:80%;
        height:900px;
    }
}
@media screen and (max-width: 600px){

}
</style>